.input_text-field {
    height: 47px;
    width: 100%;
    border: 1px solid $grey;
    border-radius: 2px;
    box-shadow: inset 0 1px 1px rgba(0,0,0,.075);
    padding: 5px 15px;
}

.input_checkbox {
    opacity: 0; // Hide checkbox.
    position: absolute;

    // Insert checkmark into wrapper.
    &:checked + div:before {
        content: "✓";
        display: flex;
        height: 100%;
        width: 100%;
        align-items: center;
        justify-content: center;
        color: $white;
        background-color: #323544;
    }
}

.input_search-field {
    display: inline-block;
    height: 56px;
    font-size: 14px;
    line-height: 1.42857143;
    color: #555;
    border: 1px solid #e5e5e5;
    border-color: $grey;
    box-shadow: inset 0 1px 1px rgba(0,0,0,.075);
    padding: 5px 15px;
}

.input_search-field-xs {
    height: 47px;
    width: 100%;
    font-size: 14px;
    background-color: rgba(0,0,0,.1);
    border: 2px solid rgba(0,0,0,.1);
    border-radius: 2px;
    box-shadow: inset 0 1px 1px rgba(0,0,0,.075);
    padding: 5px 15px;

    // Add shadow when search field is active.
    &:active {
        box-shadow: 0 0 3px $white;
    }
}

// Highlight text and search field when active.
.input_text-field, .input_search-field {
    &:focus {
        border-color: #5bc0de;
        outline: 0;
        box-shadow: inset 0 1px 1px rgba(0,0,0,.075), 0 0 8px rgba(91,192,222,.6);
        transition: border-color .15s ease-in-out,box-shadow .15s ease-in-out;
    }
}
